const INITIAL_STATE = {
  articles: [],
};

export default function articleReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "ADDARTICLE":
      const newArr = [...state.articles]; //on crée un new tab avec tout le tab article d'avant
      newArr.unshift(action.payload); //On met au tout début l'article qui arrive
      return {
        /*...state, = Pas besoin car il y a seul propriété dans l'objet INITIAL_STATE*/
        articles: newArr
      };
    case "LOADARTICLES": {
      return {
       // ...state, idem
        articles: action.payload
      }
    }
  }

  return state;
}

export const getArticles = () => (dispatch, getState) => {
  fetch("https://jsonplaceholder.typicode.com/posts")
    .then((response) => {
      return response.json();
    })
    .then((data) => {
      dispatch({
        type: "LOADARTICLES",
        payload: data,
      });
    });
};
