import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import "./Form.css";
import Navbar from "../../Components/Navbar/Navbar";
import { useRef } from "react";

export default function Form() {
  const [article, setArticle] = useState({
    title: "",
    body: "",
  });

  const dispatch = useDispatch();

  const handleForm = (e) => {
    e.preventDefault(); //Pas d'actualisation intempestive à chaque clic

    const newArticle = {
      title: article.title,
      body: article.body,
    };

    dispatch({
      type: "ADDARTICLE",
      payload: article, /*la charge utile, les données qu'on veut envoyer: article*/
    });

    setArticle({
      /**two ways data binding 
       * on lit le state avec les set()
       * value={article.title} & value={article.body}
       * Possibilité d'écrire dans les Input dè ici
       */
      title: "",
      body: "",
    })
  };

  /*technique avec LIAISONS DES FORMULAIRES ET ACTUALISATION à chaque lettre tapée*/
  const handleInputs = (e) =>{
    if (e.target.classList.contains('inp-title')) {//Si j'ai cette classe
      //On change la propriété title
      const newObjState = {...article, title: e.target.value};
      setArticle(newObjState);
    }
    else if (e.target.classList.contains('inp-body')) {
      //On change la propriété body
      const newObjState = {...article, body: e.target.value};
      setArticle(newObjState);
    }
    
  }

  const addNewTitle = (e) => {
    const newObjState = { ...article, title: e.target.value };
    setArticle(newObjState);
  };

  const addNewBody = (e) => {
    const newObjState = { ...article, body: e.target.value };
    setArticle(newObjState);
  };

  return (
    //On retourne le formulaire
    <>
      <Navbar />
      <h1 className="title-form">Écrivez un article</h1>
      <form className="container-form" onSubmit={handleForm}>
        <label htmlFor="title">Titre</label>
        <input
          value={article.title}
          onInput={addNewTitle}
          /*onChange={handleInputs}*/
          type="text"
          id="title"
          placeholder="Entrez votre nom"
          className="inp-title"
        />

        <label htmlFor="article">Votre article</label>
        <textarea
          value={article.body}
          onInput={addNewBody}
          /*onChange={handleInputs}*/
          id="article"
          placeholder="Votre article"
          className="inp-body"
        ></textarea>

        <button>Envoyer l'article</button>
      </form>
    </>
  );
}
